.info-wrapper {
  float: left;
  width: 100%;
  padding-bottom: 99px;
  position: relative;
  min-height: 100%; }

.info-header {
  background-color: #fff;
  /* layer fill content */
  -moz-box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  /* drop shadow */
  -webkit-box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  /* drop shadow */
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  /* drop shadow */
  float: left;
  width: 100%;
  border-bottom: 3px solid #edb882;
  padding: 10px 0 0;
  position: relative; }

.main-content {
  float: left;
  width: 100%;
  padding: 64px 0 0 0;
  position: relative;
  min-height: 790px; }

.logo {
  background: transparent url("images/logo_b.svg") no-repeat left top;
  background-size: 100%;
  height: 41px;
  width: 182px;
  z-index: 9999;
  display: block;
  float: none;
  margin: 0 auto; }

footer .logo {
  background-size: 80% !important; }

