  -webkit-transition: all 0.25s ease-in-out; }

.active-item {
  background: #f1e9dd;
  /* Old browsers */
  background: -moz-linear-gradient(left, #f1e9dd 0%, #faf7f2 33%, #faf7f2 35%, #faf7f2 50%, #fff 67%, #fff 78%, #fff 100%);
  /* FF3.6+ */
  background: -webkit-gradient(linear, left top, right top, color-stop(0%, #f1e9dd), color-stop(33%, #faf7f2), color-stop(35%, #faf7f2), color-stop(50%, #faf7f2), color-stop(67%, #fff), color-stop(78%, #fff), color-stop(100%, #fff));
  /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(left, #f1e9dd 0%, #faf7f2 33%, #faf7f2 35%, #faf7f2 50%, #fff 67%, #fff 78%, #fff 100%);
  /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(left, #f1e9dd 0%, #faf7f2 33%, #faf7f2 35%, #faf7f2 50%, #fff 67%, #fff 78%, #fff 100%);
  /* Opera 11.10+ */
  background: -ms-linear-gradient(left, #f1e9dd 0%, #faf7f2 33%, #faf7f2 35%, #faf7f2 50%, #fff 67%, #fff 78%, #fff 100%);
  /* IE10+ */
  background: linear-gradient(to right, #f1e9dd 0%, #faf7f2 33%, #faf7f2 35%, #faf7f2 50%, #fff 67%, #fff 78%, #fff 100%);
  /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f1e9dd', endColorstr='#fff', GradientType=1);
  /* IE6-9 */ }

